@use '../../styles/partials/global';
.about-container {
  background: white;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;

  &__title-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 60px 0;
    @include global.tablet {
      margin: 0 0 60px 0;
      padding: 60px 0 0 0;
    }
  }
  &__title {
    font-family: 'SoulmazeItalic';
    letter-spacing: 2px;
    margin: 0;
    color: #444649;
    &-letter {
      font-family: 'SoulmazeItalic';
      color: #5cc2a7;
    }
    &-hr {
      @include global.sectionTitleHR;
      background: #0aa2e7;
    }
  }
  &__column-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    @include global.tablet {
      flex-direction: row;
      width: 90%;
    }
  }
  &__profile-container {
    position: relative;
    height: 250px;
    @include global.tablet {
      margin: 0 40px;
    }
  }
  &__profile {
    margin: 0 15px 0 0;
    width: 274.953px;
    height: 238px;
    position: relative;
    z-index: 2;
    &-background {
      position: absolute;
      height: 299px;
      // z-index: -1;
      top: -16px;
      left: 15px;
      bottom: -320px;
      transform: rotate(1.1deg);
    }
  }
  &__profile-text-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    @include global.tablet {
      align-self: center;
      width: 290px;
      margin: 110px 10px;
    }
  }
  &__version-filter-container {
    display: flex;
    width: 90%;
    max-width: 290px;
    justify-content: space-between;
    margin: 50px 0 20px;
    @include global.tablet {
      margin: 0 0 20px;
      width: 100%;
      justify-content: center;
    }
  }
  &__version-filter {
    font-family: 'RalewaySB';
    text-transform: uppercase;
    text-decoration: none;
    color: #444649;
    border: 1px solid #444649;
    flex: 1;
    text-align: center;
    letter-spacing: 1px;
    padding: 16px 0;
    margin: 0 5px;
    border-radius: 4px;
    outline: 0;
    &:hover {
      cursor: pointer;
      color: #0aa2e7;
      border: 1px solid #0aa2e7;
      border-radius: 3px;
    }
    &:focus,
    &:active {
      color: #0aa2e7;
      border-top: 2px solid #0aa2e7;
      border-left: 0;
      border-right: 0;
      border-bottom: 0;
      border-radius: 0;
      outline: 0;
      -moz-outline-style: none;
    }
  }
  &__profile-text {
    font-family: 'Soulmaze';
    color: #444649;
    letter-spacing: 2px;
    line-height: 18px;
    width: 290px;
    margin: 25px 18px 35px;
    @include global.tablet {
      margin: 25px 18px 5px;
    }
    &-career {
      font-family: 'Soulmaze';
      color: #e31b6d;
    }
  }
  &__hexagon-top {
    display: none;
    @include global.tablet {
      display: flex;
      position: absolute;
      height: 370px;
      top: -91px;
      right: -50px;
      transform: rotate(90deg);
      overflow: hidden;
    }
  }
  &__hexagon-bottom {
    display: none;
    @include global.tablet {
      display: flex;
      position: absolute;
      height: 370px;
      bottom: -151px;
      left: -30px;
      transform: rotate(270deg);
      overflow: hidden;
    }
  }
}
